<template>
  <div class="cards-pile-wrapper">

    <base-card class="base-card"
    v-for="card in usersData"
    
    :key="card.input"
    :chipSource= card.chipSource
    :logoSource= card.logoSource
    :style= card.cardColor
    :cardNumber= card.inputNumber
    :fullname= card.inputName
    :MM= card.inputMonth
    :YY= card.inputYear
    :ref="card.id"
    >
    </base-card>
         
  </div>
</template>

<script>
import BaseCard from "@/components/BaseCard.vue";
export default {
  components:{
    BaseCard
  },
  data(){
    return{
        usersData: this.$root.data,
    }
  },
}
</script>

<style scoped>
.base-card{
  animation: moveUp 0.5s ease-out;
}
.base-card:hover{
  cursor: pointer;
  transform: scale(1.05) translateY(-5px);
  transition-duration: .10s;
}
/* .base-card:nth-child(2n-1):hover{ 
   transform: translateX(-100px); 
   transition-duration: 1s; 
} */
.cards-pile-wrapper{
  width: 300px;

  display: block;
  margin: 3rem 0;
  width: fit-content;
}

@keyframes moveUp{
        0%{
            opacity: 0;
            transform: translateY(40px);
        }
        50%{
            opacity: 0;
            transform: translateY(40px);
        }
        100%{
            transform: translateY(0);
        }
    }
  
</style>
        
      